import React, { useEffect } from "react";
import { motion } from "framer-motion";
import { Link, useLocation } from "react-router-dom";
import { TiSocialGithubCircular, TiSocialLinkedinCircular } from "react-icons/ti";

const classNames = (...classes: string[]) => {
	return classes.filter(Boolean).join(" ");
};

const Navbar = () => {
	const location = useLocation();
	const { pathname } = location;

	const isProjects = pathname.includes("projects");

	const linkAnimation = {
		moveDown: { y: 100, transition: { duration: 0.5 } },
		moveUp: { y: 0, transition: { duration: 0.5 } },
	};

	useEffect(() => {
		if (isProjects) {
			document.querySelector('meta[name="theme-color"]')?.setAttribute("content", "#1e293b");
			document
				.querySelector('meta[name="msapplication-navbutton-color"]')
				?.setAttribute("content", "#1e293b");
			document
				.querySelector('meta[name="apple-mobile-web-app-status-bar-style"]')
				?.setAttribute("content", "#1e293b");
		} else {
			document.querySelector('meta[name="theme-color"]')?.setAttribute("content", "#f9fafb");
			document
				.querySelector('meta[name="msapplication-navbutton-color"]')
				?.setAttribute("content", "#f9fafb");
			document
				.querySelector('meta[name="apple-mobile-web-app-status-bar-style"]')
				?.setAttribute("content", "#f9fafb");
		}
	}, [isProjects]);

	return (
		<nav className="mx-auto max-w-6xl w-full relative z-20 flex justify-between gap-10 p-4">
			<div className="flex items-center gap-3 pt-1">
				<motion.a
					initial={{ scale: 0 }}
					animate={{ scale: 1 }}
					transition={{ duration: 0.5 }}
					href="https://github.com/moelhaj"
					target="_blank"
					rel="noopener noreferrer"
					className={classNames(
						isProjects ? "text-gray-100" : "text-slate-700",
						"cursor-pointer duration-300 hover:text-indigo-600"
					)}
				>
					<TiSocialGithubCircular size={30} />
				</motion.a>
				<motion.a
					initial={{ scale: 0 }}
					animate={{ scale: 1 }}
					transition={{ duration: 0.5 }}
					href="https://www.linkedin.com/in/mo-elhaj/"
					target="_blank"
					rel="noopener noreferrer"
					className={classNames(
						isProjects ? "text-gray-100" : "text-slate-700",
						"cursor-pointer duration-300 hover:text-indigo-600"
					)}
				>
					<TiSocialLinkedinCircular size={30} />
				</motion.a>
			</div>
			<div className="p-1 relative overflow-hidden w-24 h-7 text-center text-xl font-bold tracking-tighter">
				<motion.div
					initial="moveUp"
					animate={isProjects ? "moveUp" : "moveDown"}
					variants={linkAnimation}
					className="absolute"
				>
					<Link to="/" className="text-gray-100 whitespace-nowrap block">
						About Me
					</Link>
				</motion.div>
				<motion.div
					initial="moveDown"
					animate={isProjects ? "moveDown" : "moveUp"}
					variants={linkAnimation}
					className="absolute"
				>
					<Link to="/projects" className="text-slate-700 whitespace-nowrap">
						My Work
					</Link>
				</motion.div>
			</div>
		</nav>
	);
};

export default Navbar;
