import { motion } from "framer-motion";
import Button from "./Button";

export default function GameBoard(props: any) {
	const { firstWord, secondWord, won, submitLetter, incorrectLetters, correctLetters } = props;
	// button div animation
	const buttonsAnimation = {
		enter: {
			display: "flex",
			opacity: 1,
			transition: {
				duration: 1,
				delay: 1,
			},
		},
		exit: {
			opacity: 0,
			transition: {
				duration: 1,
			},
			transitionEnd: {
				display: "none",
			},
		},
	};
	return (
		<>
			<div className="text-sm md:text-base flex flex-col md:flex-row w-full justify-center">
				<div className="mt-5 lg:mt-10 flex gap-1 flex-wrap max-w-full justify-center">
					{firstWord?.arrayOfLetters.map((letter: string, index: number) => (
						<div
							key={`${letter}-${index}`}
							className="w-5 h-5 md:w-6 md:h-6 border-b-2 border-slate-600 grid place-content-center"
						>
							{letter}
						</div>
					))}
				</div>
				<div className="hidden md:block w-5 h-5 md:w-6 md:h-6" />
				<div className="mt-5 lg:mt-10 flex gap-1 flex-wrap max-w-full justify-center">
					{secondWord?.arrayOfLetters.map((letter: string, index: number) => (
						<div
							key={`${letter}-${index}`}
							className="w-5 h-5 md:w-6 md:h-6 border-b-2 border-slate-600 grid place-content-center"
						>
							{letter}
						</div>
					))}
				</div>
			</div>
			<div className="w-full h-full overflow-hidden">
				<div className="text-sm md:text-base mt-5 lg:mt-10 gap-4 flex flex-wrap w-ful px-5 md:md: justify-center">
					{[...firstWord.removedLetters, ...secondWord.removedLetters].map(
						(letter, index) => (
							<Button
								key={`${letter}-${index}`}
								letter={letter}
								incorrectLetters={incorrectLetters}
								correctLetters={correctLetters}
								submitLetter={() => submitLetter(letter)}
							/>
						)
					)}
				</div>
				<motion.div
					initial="exit"
					animate={won ? "enter" : "exit"}
					variants={buttonsAnimation}
					className="flex justify-center mt-5"
				>
					That's Correct
				</motion.div>
			</div>
		</>
	);
}
