import { motion } from "framer-motion";
import { useEffect, useState } from "react";
import prepareWord from "../helpers/prepareWord";
import multipleIndexes from "../helpers/multipleIndexes";
import GameBoard from "./game/GameBoard";

const AnimatedTitle = (props: any) => {
	return (
		<div className="overflow-hidden relative z-20">
			<motion.h1
				animate={{ y: ["150%", "0%", "0%"] }}
				transition={{
					duration: 4,
					ease: "easeInOut",
					times: [0, 0.2, 1],
					delay: props.delay,
				}}
				className="text-3xl sm:text-4xl md:text-5xl font-bold tracking-tighter whitespace-nowrap title-height"
			>
				{props.text}
			</motion.h1>
		</div>
	);
};

const AnimatedText = (props: any) => {
	return (
		<div className="h-full overflow-hidden relative-z-20">
			<motion.h1
				animate={{ y: ["150%", "0%"] }}
				transition={{
					duration: 1,
					ease: "easeInOut",
					times: [0, 1],
					delay: props.delay,
				}}
				className="text-sm sm:text-base"
			>
				{props.children}
			</motion.h1>
		</div>
	);
};

const AnimatedDiv = (props: any) => {
	return (
		<div className="overflow-hidden relative z-20">
			<motion.div
				animate={{ y: ["150%", "0%", "0%"] }}
				transition={{
					duration: 4,
					ease: "easeInOut",
					times: [0, 0.2, 1],
					delay: props.delay,
				}}
			>
				{props.children}
			</motion.div>
		</div>
	);
};

const About = () => {
	const [won, setWon] = useState(false);
	const [incorrectLetters, setIncorrectLetters] = useState<any>([]);
	const [correctLetters, setCorrectLetters] = useState<any>([]);
	const [firstWord, setFirstWord] = useState(prepareWord("FRONTEND", 2));
	const [secondWord, setSecondWord] = useState(prepareWord("DEVELOPER", 2));

	const submitLetter = (letter: string) => {
		if (firstWord.word.includes(letter) || secondWord.word.includes(letter)) {
			setCorrectLetters([...correctLetters, letter]);
			// setFirstWord({
			// 	...firstWord,
			// 	removedLetters: firstWord.removedLetters.filter((el: string) => el !== letter),
			// });

			// setSecondWord({
			// 	...secondWord,
			// 	removedLetters: secondWord.removedLetters.filter((el: string) => el !== letter),
			// });

			const firstWordIndexes = multipleIndexes(firstWord.word, letter);
			const secondWordIndexes = multipleIndexes(secondWord.word, letter);

			if (firstWordIndexes?.length) {
				firstWordIndexes.map((index: number) => (firstWord.arrayOfLetters[index] = letter));
			} else {
				firstWord.arrayOfLetters[firstWordIndexes] = letter;
			}

			if (secondWordIndexes?.length) {
				secondWordIndexes.map(
					(index: number) => (secondWord.arrayOfLetters[index] = letter)
				);
			} else {
				secondWord.arrayOfLetters[secondWordIndexes] = letter;
			}
		} else {
			setIncorrectLetters([...incorrectLetters, letter]);
		}
	};

	useEffect(() => {
		if (!firstWord.arrayOfLetters.includes(" ") && !secondWord.arrayOfLetters.includes(" ")) {
			setWon(true);
		}
	}, [correctLetters, incorrectLetters]);

	return (
		<motion.div
			initial={{ y: "100%" }}
			animate={{ y: "0%" }}
			exit={{ opacity: 1 }}
			transition={{ duration: 0.5, ease: "easeOut" }}
			className="absolute top-0 left-0 w-full h-full bg-slate-50 text-slate-800"
		>
			<div className="mx-auto max-w-6xl w-full h-full flex justify-center items-center relative">
				<div className="flex flex-col justify-center items-center lg:flex-row p-2 md:p-3 w-full h-full gap-2 lg:gap-10">
					<div className="flex-1 p-2 flex flex-col justify-end lg:justify-start items-center lg:items-start text-center lg:text-left">
						<div className="relative my-1 sm:my-2 md:my-3">
							<AnimatedTitle delay="0.5" text="Hi there," />
						</div>
						<div className="relative my-1 md:my-2 lg:my-3">
							<AnimatedTitle delay="0.6" text="I'm Mohamed Elhaj" />
						</div>
						<div className="text-slate-800 relative z-20 mt-1 max-w-full lg:w-96 mx-auto px-2 lg:px-0 lg:mx-0">
							<AnimatedText delay="1">
								A motivated problem solver who enjoys building web applications
								using React , Redux Toolkit, Tailwind CSS, NodeJs, ExpressJs,
								Prisma, Framer Motion and more.
							</AnimatedText>
						</div>
					</div>
					{/* info */}
					<div className="flex-1 w-full h-full flex flex-col justify-start lg:justify-center">
						<div className="flex flex-col py-2 rounded-md">
							<AnimatedDiv delay="2.5">
								<GameBoard
									firstWord={firstWord}
									secondWord={secondWord}
									won={won}
									submitLetter={submitLetter}
									incorrectLetters={incorrectLetters}
									correctLetters={correctLetters}
								/>
							</AnimatedDiv>
						</div>
					</div>
				</div>
			</div>
		</motion.div>
	);
};

export default About;
