import assignsLetters from "./assignsLetters";
import { shuffle } from "./shuffleArray";

const prepareWord = (word: string, numOfRemovedLetters: number) => {
	const removedLetters: string[] = [];
	const arrayOfLetters = word.split("");
	const shuffledIndexes = shuffle([...Array(word.length).keys()]);

	[...Array(numOfRemovedLetters).keys()].forEach((index: number) => {
		removedLetters.push(arrayOfLetters[shuffledIndexes[index]]);
		arrayOfLetters[shuffledIndexes[index]] = " ";
	});
	return {
		word: word.split(""),
		arrayOfLetters,
		removedLetters: assignsLetters(removedLetters, 2),
	};
};

export default prepareWord;
