import React from "react";

export default function Button(props: any) {
	const { letter, submitLetter, incorrectLetters, correctLetters } = props;

	const classNames = (...classes: string[]) => {
		return classes.filter(Boolean).join(" ");
	};
	return (
		<span
			className={classNames(
				incorrectLetters.includes(letter)
					? "bg-rose-200 text-rose-600 cursor-default"
					: correctLetters.includes(letter)
					? "bg-green-200 text-green-600 cursor-default"
					: "bg-slate-200 hover:bg-slate-300 cursor-pointer",
				"w-6 h-6 md:w-8 md:h-8 duration-300 rounded-md grid place-content-center"
			)}
			onClick={() => submitLetter(letter)}
		>
			{letter}
		</span>
	);
}
