import { useState, useEffect } from "react";
import { TbChevronLeft, TbChevronRight } from "react-icons/tb";

export default function Carousel({
	children: slides,
	autoSlide = true,
	autoSlideInterval = 5000,
}: any) {
	const [curr, setCurr] = useState(0);

	const prev = () => setCurr(curr => (curr === 0 ? slides.length - 1 : curr - 1));
	const next = () => setCurr(curr => (curr === slides.length - 1 ? 0 : curr + 1));

	useEffect(() => {
		if (!autoSlide) return;
		const slideInterval = setInterval(next, autoSlideInterval);
		return () => clearInterval(slideInterval);
	}, []);
	return (
		<div className="overflow-hidden relative">
			<div
				className="flex transition-transform ease-out duration-500"
				style={{ transform: `translateX(-${curr * 100}%)` }}
			>
				{slides}
			</div>
			<div className="absolute inset-0 flex items-center justify-between p-2 lg:p-3">
				<button
					onClick={prev}
					className="p-1 rounded-full shadow bg-white/30 text-gray-800 hover:bg-white duration-300"
				>
					<TbChevronLeft size={20} />
				</button>
				<button
					onClick={next}
					className="p-1 rounded-full shadow bg-white/30 text-gray-800 hover:bg-white duration-300"
				>
					<TbChevronRight size={20} />
				</button>
			</div>

			<div className="absolute bottom-0 left-0 right-0 h-10 bg-black opacity-30 shadow-2xl blur-lg w-full" />

			<div className="absolute bottom-2 right-0 left-0">
				<div className="flex items-center justify-center gap-2">
					{slides.map((_: any, i: number) => (
						<div
							key={`slides-${i}`}
							className={`
              transition-all w-2 h-2 bg-white shadow-md rounded-full
              ${curr === i ? "p-1.5" : "bg-opacity-50"}
            `}
						/>
					))}
				</div>
			</div>
		</div>
	);
}
