import { shuffle } from "./shuffleArray";
const assignsLetters = (letters: string[], lettersToAdd: number) => {
	const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
	let count = 0;

	function addLetter() {
		let randomInt = Math.floor(Math.random() * 25) + 1;

		if (!letters.includes(alphabet[randomInt])) {
			letters.push(alphabet[randomInt]);
		} else {
			addLetter();
		}

		if (count >= lettersToAdd) return;
		count = count + 1;
		addLetter();
	}

	addLetter();

	return shuffle(letters);
};

export default assignsLetters;
